<template id="">
  <div class="test">
    <input ref="phoneNum" type="text" @input="phoneOnInput" placeholder="请输入手机号">
    <div class="text">
      <span>input的值：{{this.phone}}</span>
    </div>
  </div>
</template>

<script type="text/javascript">
  export default {
    data() {
      return {
        phone: ''
      }
    },
    methods: {
      phoneOnInput(e) {
        console.log('e-text-tm',e.target.value);
        const value = e.target.value.trim()
        this.phone = value
        console.log('phone-text-tm',this.phone);
      }
    }
  }
</script>
<style>
  .test {
    margin: 20px 40px;
  }
  .text {
    margin: 20px 40px;
  }
</style>



